<template>
  <div id="app">
    <h1>Drag and Drop Demo</h1>
    <ImageDrop @image-dropped="onImageDropped" />
    <img v-if="imageSrc" :src="imageSrc" alt="Dropped image" />
  </div>
</template>

<script>
import ImageDrop from './components/ImageDrop.vue';

export default {
  name: 'App',
  components: {
    ImageDrop,
  },
  data() {
    return {
      imageSrc: null,
    };
  },
  methods: {
    onImageDropped(src) {
      this.imageSrc = src;
    },
  },
};
</script>

