<template>
    <div class="container">
      <div class="dropzone" @dragover.prevent @drop="onDrop">
        <p v-if="!imageLoaded">여기에 이미지를 드래그 앤 드롭하세요.</p>
        <img v-if="imageLoaded" :src="imageSrc" alt="Dropped image" />
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        imageLoaded: false,
        imageSrc: null,
      };
    },
    methods: {
      onDrop(event) {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        if (file.type.startsWith('image/')) {
          const reader = new FileReader();
          reader.onload = (e) => {
            const img = new Image();
            img.onload = () => {
              if (img.width > 500 || img.height > 400) {
                alert('500x400 이상의 이미지는 업로드할 수 없습니다.');
              } else {
                this.imageSrc = e.target.result;
                this.imageLoaded = true;
              }
            };
            img.src = e.target.result;
          };
          reader.readAsDataURL(file);
        } else {
          alert('이미지 파일만 업로드 가능합니다.');
        }
      },
    },
  };
  </script>
    
  <style scoped>
    .container {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 20px; /*드래그 앤 드롭박스와 버튼 간 간격 설정*/
    }
    .dropzone {
      width: 600px;
      height: 600px;
      border: 2px dashed #ccc;
      position: relative;
      overflow: hidden;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
    }
    .dropzone p {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
      }
  </style>
    